div.app{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    background-color: #ddd;
    color: #222;
}

header{
    flex-basis: 30vh;
    /* background-color: #eee; */
}

main{
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
    /* background-color: #555; */
}

aside{
    min-width: 240px;
    /* background-color: #999; */
}

section.blog{
    /* background-color: #333; */
    flex-grow: 1;
}

footer{
    margin-bottom: 50px;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    background-color: black;
    color: white;
}