.contact textarea{
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.5;
}

.contact button{
    display: block;
    border-bottom: 2px solid #000;
    background-color: #eee;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    margin: 0 20% 0 auto;
    transition: .3s;
}

.contact button:hover{
    background-color: #222;
    color: #fff;
}