*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html{
  font-family: 'open-spans', sans-serif;
  font-size: 20px;
}
div.app{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    background-color: #ddd;
    color: #222;
}

header{
    flex-basis: 30vh;
    /* background-color: #eee; */
}

main{
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
    /* background-color: #555; */
}

aside{
    min-width: 240px;
    /* background-color: #999; */
}

section.blog{
    /* background-color: #333; */
    flex-grow: 1;
}

footer{
    margin-bottom: 50px;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    background-color: black;
    color: white;
}
header{
    position: relative;
    overflow: hidden;
}

header img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main{
    /* background-color: yellow; */
    padding: 0 30px 30px 0;
}

.main ul{
    list-style: none;
}

nav.main a{
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: 0.3s;
}

nav.main a:hover{
    transform: translateX(20px);
}

nav.main a.active{
    border-left: 10px solid red;
    transform: translateX(0);
}

nav.main li:nth-child(1) a.active{
    border-left-color: green;
}

nav.main li:nth-child(3) a.active{
    border-left-color: orange;
}

nav.main li:nth-child(4) a.active{
    border-left-color: turquoise;
}


.contact textarea{
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.5;
}

.contact button{
    display: block;
    border-bottom: 2px solid #000;
    background-color: #eee;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    margin: 0 20% 0 auto;
    transition: .3s;
}

.contact button:hover{
    background-color: #222;
    color: #fff;
}
.products h2{
    margin-bottom: 20px;
}

.products ul{
    list-style: square;
    padding-left: 30px;
}
.products li{
    margin-bottom: 20px;
}

.products a{
    text-decoration: none;
    color: #333;
}
.products a:hover{
    text-decoration: underline;
    color: #000;
}
footer span{
    color: red;
}

footer p{
    font-size: 14px;
}
